<template>
  <component
    :is="listTagType"
    class="space-y-4 ps-6"
    :class="`list-${list_style}`"
  >
    <li v-for="(value, index) in question.values" :key="index">
      {{ value.label }}
    </li>
  </component>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import Survey from "@/models/Survey";

export default {
  name: "SurveyOutputList",
  props: {
    template: {
      type: [Template, Survey],
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
  },
  data() {
    return {
      list_style: "disc",
    };
  },
  created() {
    this.list_style = this.question.getOption("list_style", "disc");
  },
  computed: {
    listTagType() {
      if (
        [
          "decimal",
          "decimal-leading-zero",
          "upper-latin",
          "lower-latin",
          "upper-roman",
          "lower-roman",
        ].includes(this.list_style)
      ) {
        return "ol";
      }

      return "ul";
    },
  },
};
</script>
