<template>
  <p
    v-if="labelShowAble"
    key="isSurveyQuestionLabelShowAble"
    class="mb-3 text-lg font-medium"
  >
    <span v-if="question.question_order" key="isQuestionTypeInput">{{
      $t("labels.survey_question_order", {
        order: question.question_order,
      })
    }}</span>
    {{ question.label }}
  </p>

  <component
    :is="`Survey${question.component}`"
    :template="template"
    :question="question"
  ></component>

  <div
    v-if="question.description"
    key="isQuestionHasDescription"
    class="mt-3 text-xs+"
  >
    {{ question.description }}
  </div>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import Survey from "@/models/Survey";
import SurveyInputText from "@/components/SurveyInputs/Input/SurveyInputText.vue";
import SurveyInputNumber from "@/components/SurveyInputs/Input/SurveyInputNumber.vue";
import SurveyInputRadioGroup from "@/components/SurveyInputs/Input/SurveyInputRadioGroup.vue";
import SurveyInputCheckboxGroup from "@/components/SurveyInputs/Input/SurveyInputCheckboxGroup.vue";
import SurveyInputDate from "@/components/SurveyInputs/Input/SurveyInputDate.vue";
import SurveyInputMultipleRadioGroup from "@/components/SurveyInputs/Input/SurveyInputMultipleRadioGroup.vue";
import SurveyInputRange from "@/components/SurveyInputs/Input/SurveyInputRange.vue";
import SurveyOutputDescription from "@/components/SurveyInputs/Output/SurveyOutputDescription.vue";
import SurveyOutputImage from "@/components/SurveyInputs/Output/SurveyOutputImage.vue";
import SurveyOutputList from "@/components/SurveyInputs/Output/SurveyOutputList.vue";

export default {
  name: "SurveyInput",
  components: {
    SurveyInputText,
    SurveyInputNumber,
    SurveyInputRadioGroup,
    SurveyInputCheckboxGroup,
    SurveyInputDate,
    SurveyInputMultipleRadioGroup,
    SurveyInputRange,
    SurveyOutputDescription,
    SurveyOutputImage,
    SurveyOutputList,
  },
  props: {
    template: {
      type: [Template, Survey],
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
  },
  computed: {
    labelShowAble() {
      let components = ["InputMultipleRadioGroup", "OutputImage"];

      return !components.includes(this.question.component);
    },
  },
};
</script>
