<template>
  <div class="flex gap-6" :class="flexType">
    <p
      class="whitespace-pre-line"
      :class="{
        'order-1': ['top', 'left'].includes(
          question.getOption('image_position', 'top')
        ),
      }"
    >
      {{ question.value }}
    </p>

    <div
      v-if="question.getOption('image')"
      key="isDescriptionHasImage"
      class="shrink-0"
      :class="{
        'order-0': ['top', 'left'].includes(
          question.getOption('image_position', 'top')
        ),
        'max-w-[50%]': ['left', 'right'].includes(
          question.getOption('image_position', 'top')
        ),
      }"
    >
      <img
        :src="question.getOptionImage()"
        :title="$t('labels.uploaded_image')"
        :alt="$t('labels.description_image')"
        class="max-w-full"
      />
    </div>
  </div>
</template>

<script>
import Template from "@/models/Template";
import Question from "@/models/Question";
import Survey from "@/models/Survey";

export default {
  name: "SurveyOutputDescription",
  props: {
    template: {
      type: [Template, Survey],
      required: true,
    },
    question: {
      type: Question,
      required: true,
    },
  },
  computed: {
    flexType() {
      if (this.question.getOption("image")) {
        let position = this.question.getOption("image_position");
        if (["top", "bottom"].includes(position)) {
          return "flex-col";
        }

        if (["right", "left"].includes(position)) {
          return "flex-row";
        }
      }

      return "flex-col";
    },
  },
};
</script>
